.sidebar {
    background-color: aqua;

}

.sidebar.open {
    background-color: #f17fcb;
}

.background {
    background-color: grey;
}

.nav {
    background-color: white;
    height: 100px;
    width: 30%;
    color:#282c34;
    font-size: 30px;
}

.nav a {
    color: #7fb8f1;
    text-decoration: underline;
}

.nav a:hover {
    color: #f17fcb;
    text-decoration: underline;
}

.nav a.active {
    color: #f1c97f;
    text-decoration: underline;
}
.close {
    background-color: #7ff18e;
}

.close img {
    border: #7ff18e;
}
